'use client';

import { useEffect, useState } from 'react';
import { EbikeModel } from '@/src/lib/ebikemodels/ebikeModelTypes';
import {
  collection,
  deleteDoc,
  doc,
  getDocs,
  query,
  where,
} from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import NotificationDialog from '@/src/components/ui/NotificationDialog';
import { Button } from '@/src/components/ui/button';
import Image from 'next/image';

interface Props {
  companyId: string;
  onEdit: (model: EbikeModel) => void;
  onReload?: () => void;
  reloadTrigger?: boolean; // ‚úÖ th√™m prop
}

export default function EbikeModelTable({
  companyId,
  onEdit,
  onReload,
  reloadTrigger,
}: Props) {
  const [models, setModels] = useState<EbikeModel[]>([]);
  const [dialog, setDialog] = useState({
    open: false,
    title: '',
    onConfirm: undefined as (() => void) | undefined,
  });

  const fetchModels = async () => {
    try {
      const q = query(
        collection(db, 'ebikeModels'),
        where('companyId', '==', companyId)
      );
      const snapshot = await getDocs(q);
      const data: EbikeModel[] = snapshot.docs.map((docSnap) => ({
        id: docSnap.id,
        ...(docSnap.data() as Omit<EbikeModel, 'id'>),
      }));
      setModels(data);
      onReload?.(); // ‚úÖ g·ªçi l·∫°i callback sau khi reload
    } catch (err) {
      console.error('‚ùå Error fetching ebike models:', err);
    }
  };

  useEffect(() => {
    if (companyId) {
      fetchModels();
    }
  }, [companyId, reloadTrigger]); // ‚úÖ l·∫Øng nghe reloadTrigger

  const confirmDelete = (id: string) => {
    setDialog({
      open: true,
      title: 'Delete this Ebike Model?',
      onConfirm: async () => {
        try {
          await deleteDoc(doc(db, 'ebikeModels', id));
          await fetchModels();
        } catch (err) {
          console.error('‚ùå Failed to delete ebike model:', err);
        } finally {
          setDialog({ open: false, title: '', onConfirm: undefined });
        }
      },
    });
  };

  return (
    <div className="bg-white p-6 rounded-xl shadow overflow-x-auto mt-6">
      <h2 className="text-xl font-semibold mb-4">Ebike Models</h2>

      <table className="min-w-full text-sm border border-gray-200">
        <thead className="bg-gray-100 text-gray-700">
          <tr>
            <th className="px-3 py-2 border">Image</th>
            <th className="px-3 py-2 border">Name</th>
            <th className="px-3 py-2 border">Battery</th>
            <th className="px-3 py-2 border">Motor</th>
            <th className="px-3 py-2 border">Top Speed</th>
            <th className="px-3 py-2 border">Range</th>
            <th className="px-3 py-2 border">Weight</th>
            <th className="px-3 py-2 border">Max Load</th>
            <th className="px-3 py-2 border">$/Hour</th>
            <th className="px-3 py-2 border">$/Day</th>
            <th className="px-3 py-2 border">$/Week</th>
            <th className="px-3 py-2 border">$/Month</th>
            <th className="px-3 py-2 border">Available</th>
            <th className="px-3 py-2 border">Actions</th>
          </tr>
        </thead>
        <tbody>
          {models.map((model) => (
            <tr key={model.id} className="hover:bg-gray-50">
              <td className="px-3 py-2 border">
                {model.imageUrl ? (
                  <Image
                    src={model.imageUrl}
                    alt={model.name}
                    width={60}
                    height={40}
                    className="rounded object-cover"
                  />
                ) : (
                  <span className="text-gray-400 italic">No Image</span>
                )}
              </td>
              <td className="px-3 py-2 border font-medium">{model.name}</td>
              <td className="px-3 py-2 border">{model.batteryCapacity} Wh</td>
              <td className="px-3 py-2 border">{model.motorPower} W</td>
              <td className="px-3 py-2 border">{model.topSpeed} km/h</td>
              <td className="px-3 py-2 border">{model.range} km</td>
              <td className="px-3 py-2 border">{model.weight} kg</td>
              <td className="px-3 py-2 border">{model.maxLoad ?? '-'} kg</td>
              <td className="px-3 py-2 border">{model.pricePerHour ? `$${model.pricePerHour}` : '-'}</td>
              <td className="px-3 py-2 border">${model.pricePerDay}</td>
              <td className="px-3 py-2 border">{model.pricePerWeek ? `$${model.pricePerWeek}` : '-'}</td>
              <td className="px-3 py-2 border">{model.pricePerMonth ? `$${model.pricePerMonth}` : '-'}</td>
              <td className="px-3 py-2 border">
                <span className={`text-xs font-semibold px-2 py-1 rounded ${
                  model.available ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
                }`}>
                  {model.available ? 'Yes' : 'No'}
                </span>
              </td>
              <td className="px-3 py-2 border flex flex-col sm:flex-row gap-1">
                <Button size= "sm" variant="default" onClick={() => onEdit(model)}>Edit</Button>
                <Button size= "sm" variant="destructive" onClick={() => confirmDelete(model.id)}>Delete</Button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <NotificationDialog
        open={dialog.open}
        title={dialog.title}
        type="confirm"
        onConfirm={dialog.onConfirm}
        onClose={() => setDialog((prev) => ({ ...prev, open: false }))}
      />
    </div>
  );
}
