import { useState } from 'react';
import {
  addDoc,
  collection,
  doc,
  serverTimestamp,
  updateDoc,
  getDocs,
  query,
  where
} from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { EbikeModel } from '@/src/lib/ebikemodels/ebikeModelTypes';

export function useEbikeModelForm(
  companyId: string,
  setModels: (models: EbikeModel[]) => void,
  onSaveComplete?: () => void
) {
  const [loading, setLoading] = useState(false);
  const [isUpdateModeModel, setIsUpdateModeModel] = useState(false);

  const [newEbikeModel, setNewEbikeModel] = useState<EbikeModel>(getEmptyModel(companyId));

  function getEmptyModel(companyId: string): EbikeModel {
    return {
      id: '',
      companyId,
      name: '',
      description: '',
      batteryCapacity: 0,
      motorPower: 0,
      topSpeed: 0,
      range: 0,
      weight: 0,
      maxLoad: undefined,
      pricePerDay: 0,
      pricePerHour: undefined,
      pricePerWeek: undefined,
      pricePerMonth: undefined,
      imageUrl: '',
      available: true,
    };
  }

  const handleChange = <K extends keyof EbikeModel>(
    key: K,
    value: EbikeModel[K]
  ) => {
    setNewEbikeModel((prev) => ({ ...prev, [key]: value }));
  };

  const resetForm = () => {
    setNewEbikeModel(getEmptyModel(companyId));
    setIsUpdateModeModel(false);
  };

  const setEditModel = (model: EbikeModel) => {
    setNewEbikeModel(model);
    setIsUpdateModeModel(true);
  };

  const fetchModels = async () => {
    const q = query(collection(db, 'ebikeModels'), where('companyId', '==', companyId));
    const snapshot = await getDocs(q);
    const list = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() })) as EbikeModel[];
    setModels(list);
  };

  const handleSave = async () => {
    if (!newEbikeModel.name || !newEbikeModel.description) {
      alert('Please fill out required fields.');
      return;
    }

    setLoading(true);

    const data = {
      ...newEbikeModel,
      companyId,
      batteryCapacity: Number(newEbikeModel.batteryCapacity),
      motorPower: Number(newEbikeModel.motorPower),
      topSpeed: Number(newEbikeModel.topSpeed),
      range: Number(newEbikeModel.range),
      weight: Number(newEbikeModel.weight),
      pricePerDay: Number(newEbikeModel.pricePerDay),
      available: !!newEbikeModel.available,
      updatedAt: serverTimestamp(),
      createdAt: isUpdateModeModel
        ? newEbikeModel.createdAt
        : serverTimestamp(),
      ...(newEbikeModel.maxLoad !== undefined
        ? { maxLoad: Number(newEbikeModel.maxLoad) }
        : {}),
      ...(newEbikeModel.pricePerHour !== undefined
        ? { pricePerHour: Number(newEbikeModel.pricePerHour) }
        : {}),
      ...(newEbikeModel.pricePerWeek !== undefined
        ? { pricePerWeek: Number(newEbikeModel.pricePerWeek) }
        : {}),
      ...(newEbikeModel.pricePerMonth !== undefined
        ? { pricePerMonth: Number(newEbikeModel.pricePerMonth) }
        : {}),
      ...(newEbikeModel.imageUrl ? { imageUrl: newEbikeModel.imageUrl } : {}),
    };

    try {
      if (isUpdateModeModel && newEbikeModel.id) {
        await updateDoc(doc(db, 'ebikeModels', newEbikeModel.id), data);
      } else {
        const docRef = await addDoc(collection(db, 'ebikeModels'), {
          ...data,
          id: '',
        });
        await updateDoc(docRef, { id: docRef.id });
      }

      await fetchModels();
      resetForm();
      onSaveComplete?.();
    } catch (err) {
      console.error('‚ùå Failed to save ebike model:', err);
    } finally {
      setLoading(false);
    }
  };

  return {
    loading,
    isUpdateModeModel,
    setIsUpdateModeModel,
    newEbikeModel,
    setNewEbikeModel,
    handleChange,
    handleSave,
    resetForm,
    setEditModel,
  };
}
