'use client';

import { Input } from '@/src/components/ui/input';
import { Textarea } from '@/src/components/ui/textarea';
import { Button } from '@/src/components/ui/button';
import { SimpleSelect } from '@/src/components/ui/select';
import { EbikeStatus, Ebike } from '@/src/lib/ebikes/ebikeTypes';
import { EbikeModel } from '@/src/lib/ebikemodels/ebikeModelTypes';
import { RentalStation } from '@/src/lib/rentalStations/rentalStationTypes';
import { useUser } from '@/src/context/AuthContext'; // c·∫ßn ƒë·ªÉ l·∫•y role, ki·ªÉm tra admin

interface Props {
  companyId: string;
  newEbike: Ebike;
  setNewEbike: (bike: Ebike) => void;
  models: EbikeModel[];
  stations: RentalStation[];
  isUpdateMode: boolean;
  setIsUpdateMode: (v: boolean) => void;
  setEbikes: (list: Ebike[]) => void;
  onSaveComplete?: () => void;
  showStationSelect: boolean;
}

const statusOptions: EbikeStatus[] = [
  'Available',
  'In Use',
  'Reserved',
  'Under Maintenance',
  'Sold',
  'Broken',
];

export default function EbikeForm({
  companyId,
  newEbike,
  setNewEbike,
  models,
  stations,
  isUpdateMode,
  setIsUpdateMode,
  setEbikes,
  onSaveComplete,
  showStationSelect,
}: Props) {
  const { role } = useUser();
  const isAdmin = role === 'admin';

  const handleChange = <K extends keyof Ebike>(key: K, value: Ebike[K]) => {
    const updated = { ...newEbike, [key]: value };
    setNewEbike(updated);
  };

  const handleSubmit = async () => {
    if (!newEbike.modelId || !newEbike.serialNumber || !newEbike.vehicleID || (showStationSelect && !newEbike.stationId)) {
      alert('Please fill all required fields');
      return;
    }

    const payload = {
      ...newEbike,
      batteryCapacity: Number(newEbike.batteryCapacity),
      range: Number(newEbike.range),
      odo: Number(newEbike.odo),
      pricePerDay: Number(newEbike.pricePerDay),
      ...(newEbike.pricePerHour !== undefined ? { pricePerHour: Number(newEbike.pricePerHour) } : {}),
      ...(newEbike.pricePerWeek !== undefined ? { pricePerWeek: Number(newEbike.pricePerWeek) } : {}),
      ...(newEbike.pricePerMonth !== undefined ? { pricePerMonth: Number(newEbike.pricePerMonth) } : {}),
    };

    try {
      const { saveEbike } = await import('@/src/lib/ebikes/ebikeService');
      const updatedList = await saveEbike(payload, isUpdateMode);
      setEbikes(updatedList);
      setIsUpdateMode(false);
      onSaveComplete?.();
    } catch (err) {
      console.error('‚ùå Failed to save ebike:', err);
    }
  };

  return (
    <div className="bg-white rounded-xl shadow p-6 mt-6">
      <h2 className="text-xl font-semibold mb-4">
        {isUpdateMode ? 'Edit Ebike' : 'Add New Ebike'}
      </h2>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        {isAdmin && (
          <Input
            placeholder="Company ID (Admin nh·∫≠p th·ªß c√¥ng ho·∫∑c t·ª± ƒë·ªông ch·ªçn sau)"
            value={newEbike.companyId}
            onChange={(e) => handleChange('companyId', e.target.value)}
          />
        )}

        <SimpleSelect
          options={models.map((m) => ({ label: m.name, value: m.id }))}
          placeholder="Select Model"
          value={newEbike.modelId || ''}
          onChange={(val) => handleChange('modelId', val)}
        />

        {showStationSelect && (
          <SimpleSelect
            options={stations.map((s) => ({ label: s.name, value: s.id }))}
            placeholder="Select Station"
            value={newEbike.stationId || ''}
            onChange={(val) => handleChange('stationId', val)}
          />
        )}

        <Input placeholder="Serial Number" value={newEbike.serialNumber} onChange={(e) => handleChange('serialNumber', e.target.value)} />
        <Input placeholder="Vehicle ID" value={newEbike.vehicleID} onChange={(e) => handleChange('vehicleID', e.target.value)} />
        <Input placeholder="Plate Number" value={newEbike.plateNumber} onChange={(e) => handleChange('plateNumber', e.target.value)} />
        <Input
          type="number"
          placeholder="ODO (km)"
          value={newEbike.odo || ''}
          onChange={(e) => handleChange('odo', isNaN(e.target.valueAsNumber) ? 0 : e.target.valueAsNumber)}
        />
        <Input placeholder="Color" value={newEbike.color} onChange={(e) => handleChange('color', e.target.value)} />
        <Textarea placeholder="Current Location" value={newEbike.currentLocation} onChange={(e) => handleChange('currentLocation', e.target.value)} />

        <SimpleSelect
          options={statusOptions.map((s) => ({ label: s, value: s }))}
          placeholder="Select Status"
          value={newEbike.status}
          onChange={(val) => handleChange('status', val as EbikeStatus)}
        />

        <Input
          type="number"
          placeholder="Battery Capacity (Wh)"
          value={newEbike.batteryCapacity || ''}
          onChange={(e) => handleChange('batteryCapacity', isNaN(e.target.valueAsNumber) ? 0 : e.target.valueAsNumber)}
        />
        <Input
          type="number"
          placeholder="Range (km)"
          value={newEbike.range || ''}
          onChange={(e) => handleChange('range', isNaN(e.target.valueAsNumber) ? 0 : e.target.valueAsNumber)}
        />

        <Input
          type="number"
          placeholder="Price/Hour"
          value={newEbike.pricePerHour ?? ''}
          onChange={(e) => handleChange('pricePerHour', isNaN(e.target.valueAsNumber) ? undefined : e.target.valueAsNumber)}
        />
        <Input
          type="number"
          placeholder="Price/Day"
          value={newEbike.pricePerDay || ''}
          onChange={(e) => handleChange('pricePerDay', isNaN(e.target.valueAsNumber) ? 0 : e.target.valueAsNumber)}
        />
        <Input
          type="number"
          placeholder="Price/Week"
          value={newEbike.pricePerWeek ?? ''}
          onChange={(e) => handleChange('pricePerWeek', isNaN(e.target.valueAsNumber) ? undefined : e.target.valueAsNumber)}
        />
        <Input
          type="number"
          placeholder="Price/Month"
          value={newEbike.pricePerMonth ?? ''}
          onChange={(e) => handleChange('pricePerMonth', isNaN(e.target.valueAsNumber) ? undefined : e.target.valueAsNumber)}
        />
        {/* ‚úÖ Ghi ch√∫ th√™m cho xe */}
        <Textarea
          placeholder="Note (optional)"
          value={newEbike.note || ''}
          onChange={(e) => handleChange('note', e.target.value)}
        />
      </div>

      <Button onClick={handleSubmit} className="mt-6">
        {isUpdateMode ? 'Save Changes' : 'Add Ebike'}
      </Button>
    </div>
  );
}
