'use client';

import { Input } from '@/src/components/ui/input';
import { Textarea } from '@/src/components/ui/textarea';
import { Button } from '@/src/components/ui/button';
import { Checkbox } from '@/src/components/ui/checkbox';
import { Label } from '@/src/components/ui/label';
import { EbikeModel } from '@/src/lib/ebikemodels/ebikeModelTypes';

interface Props {
  companyId: string;
  newEbikeModel: EbikeModel;
  handleChange: <K extends keyof EbikeModel>(key: K, value: EbikeModel[K]) => void;
  handleSave: () => void;
  isUpdateModeModel: boolean;
  loading: boolean;
  onSaveComplete?: () => void;
}

export default function EbikeModelForm({
  companyId,
  newEbikeModel,
  handleChange,
  handleSave,
  isUpdateModeModel,
  loading,
}: Props) {
  return (
    <div className="bg-white rounded-xl shadow p-6 mt-6">
      <h2 className="text-xl font-semibold mb-4">
        {isUpdateModeModel ? 'Edit Ebike Model' : 'Add New Ebike Model'}
      </h2>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Input
          placeholder="Model Name"
          value={newEbikeModel.name}
          onChange={(e) => handleChange('name', e.target.value)}
        />
        <Textarea
          placeholder="Description"
          value={newEbikeModel.description}
          onChange={(e) => handleChange('description', e.target.value)}
        />
        <Input
          type="number"
          placeholder="Battery Capacity (Wh)"
          value={newEbikeModel.batteryCapacity || ''}
          onChange={(e) =>
            handleChange('batteryCapacity', e.target.valueAsNumber || 0)
          }
        />
        <Input
          type="number"
          placeholder="Motor Power (W)"
          value={newEbikeModel.motorPower || ''}
          onChange={(e) =>
            handleChange('motorPower', e.target.valueAsNumber || 0)
          }
        />
        <Input
          type="number"
          placeholder="Top Speed (km/h)"
          value={newEbikeModel.topSpeed || ''}
          onChange={(e) => handleChange('topSpeed', e.target.valueAsNumber || 0)}
        />
        <Input
          type="number"
          placeholder="Range (km)"
          value={newEbikeModel.range || ''}
          onChange={(e) => handleChange('range', e.target.valueAsNumber || 0)}
        />
        <Input
          type="number"
          placeholder="Weight (kg)"
          value={newEbikeModel.weight || ''}
          onChange={(e) => handleChange('weight', e.target.valueAsNumber || 0)}
        />
        <Input
          type="number"
          placeholder="Max Load (kg)"
          value={newEbikeModel.maxLoad || ''}
          onChange={(e) =>
            handleChange('maxLoad', e.target.valueAsNumber || undefined)
          }
        />
        <Input
          type="number"
          placeholder="Price Per Hour ($)"
          value={newEbikeModel.pricePerHour || ''}
          onChange={(e) =>
            handleChange('pricePerHour', e.target.valueAsNumber || undefined)
          }
        />
        <Input
          type="number"
          placeholder="Price Per Day ($)"
          value={newEbikeModel.pricePerDay || ''}
          onChange={(e) =>
            handleChange('pricePerDay', e.target.valueAsNumber || 0)
          }
        />
        <Input
          type="number"
          placeholder="Price Per Week ($)"
          value={newEbikeModel.pricePerWeek || ''}
          onChange={(e) =>
            handleChange('pricePerWeek', e.target.valueAsNumber || undefined)
          }
        />
        <Input
          type="number"
          placeholder="Price Per Month ($)"
          value={newEbikeModel.pricePerMonth || ''}
          onChange={(e) =>
            handleChange('pricePerMonth', e.target.valueAsNumber || undefined)
          }
        />
        <Input
          placeholder="Image URL"
          value={newEbikeModel.imageUrl || ''}
          onChange={(e) => handleChange('imageUrl', e.target.value)}
        />
        <div className="flex items-center space-x-2">
          <Checkbox
            checked={newEbikeModel.available}
            onCheckedChange={(checked) =>
              handleChange('available', !!checked)
            }
          />
          <Label>Available for rent?</Label>
        </div>
      </div>

      <Button onClick={handleSave} className="mt-6" disabled={loading}>
        {loading ? 'Saving...' : isUpdateModeModel ? 'Save Changes' : 'Add Model'}
      </Button>
    </div>
  );
}
